
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import { mapState, mapMutations } from "vuex";
export default {
name: 'topiclist',
props: ['name'],
components: {
Cell,Group,Scroller,Tab,TabItem
},
data() {
return {
lists: [],

page: 1,

limit: 15,

pullupConfig: {
content: '上拉加载更多',
downContent:'松开进行加载',
upContent:'上拉加载更多',
loadingContent: '加载中...'
},

pulldownConfig:{
content: '下拉刷新',
downContent:'松开刷新数据',
upContent:'下拉刷新',
loadingContent:'加载中...'
}
}
},
computed:{
...mapState(['topics'])
},

methods: {

freshData:function () {
var app=this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function (response) {
if (response.data && response.data.success) {
app.lists=response.data.data;

app.$refs.pullup.donePulldown();
}
}) ;
},

getDescByTab(tab){
return this.$store.getters.getTopicDescByTab(tab);
},
...mapMutations(["mutationLogin","mutationLoginname","mutationAccessToken"]),

routeto(index) {








this.$router.push('/topiclist/' + this.topics[index].name);
},

loadMoreData() {
this.page ++;
var app = this;
this.$http.get('/topics',{
params: {
tab: this.name,
page: this.page,
limit: this.limit
}
}).then(function(response) {
if(response.data.success) {
app.lists = app.lists.concat(response.data.data);


app.$refs.pullup.donePullup();
}
})
}
},


beforeRouteEnter(to,from,next) {





next(function(vm) {

vm.$http.get('/topics',{
params: {
tab: to.params.name,
page: vm.page,
limit: vm.limit
}
}).then(function(response) {
if(response.data.success) {
vm.lists = response.data.data;
}
});
});
},

beforeRouteUpdate(to,from,next) {
var app = this;

this.$http.get('/topics',{
params: {
tab: to.params.name,
page: 1,
limit: this.limit
}
}).then(function(response) {
if(response.data.success) {

app.lists = response.data.data;
}
})

next();
}
}



